.navFullContainer {
    display: flex;
    background-color: #0000006b;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 10000;
}

.menuAction {
    width: 40px;
    height: 40px;
    background-color: white;
    z-index: 1000000 !important;
    position: absolute;
    right: 25px;
    bottom: 25px;
    border-radius: 25px;
    overflow: hidden;

    img {
        width: 25px;
        margin-top: 12px;
        margin-left: 7px;
    }

    .menuDots {
        color: black;
        font-weight: 900;
        font-size: 26px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: -6px;
    }
}

.dropMenu {
    z-index: 1000000 !important;
    background-color: white;
    border-radius: 12px;
    height: fit-content;
    width: 175px;
    display: flex;
    padding: 10px;
    flex-direction: column;
    position: absolute;
    right: 25px;
    bottom: 75px;

    a:hover {
        text-decoration: none;
        color: white !important;
    }

    .largeItem {
        color: black;
        font-weight: 800;
        font-size: 30px;
        padding: 5px;
        // margin: 3px 0;
        border-radius: 10px;
        // border-style: solid;
        // border-width: 1px;
        // border-color: black;
        text-align: center;
        transition: 0.4s;
        background: linear-gradient(45deg, var(--c1, #fff), var(--c2, #fff) 51%, var(--c1, #65f6a6)) var(--x, 0)/ 300%;
    }

    .largeItem:hover {
        --x: 100%;
    }

    .buy:hover {
        --c1: #f67465;
        --c2: #e3f665;
    }
    
    .play:hover {
        --c1: #9a65f6;
        --c2: #f665dc;
    }

    .learn:hover {
        --c1: #65f6a6;
        --c2: #6598f6;
    }

    .smallItem {
        margin-right: 10px;
        transition: 0.4s;
    }

    .navItem {
        
    }
    
    .iconContainer {
        margin-top: 15px;
        display: flex;
        flex-direction: row;
        justify-content: center;

        a:hover {
            color: black !important;
        }
    }
}

@media screen and (max-width: 600px) {

    .menuAction {
        bottom: 15px;
        width: 35px;
        height: 35px;

        .menuDots {
            line-height: 35px;
            font-size: 16pt;
        }
    }

    .menuAction {
        img {
            width: 21px;
            margin-top: 7px;
            margin-left: 7px;
        }
    }
}
